:root, :host {
    /**
     * Editor background
     */
    --b-task-board-simple-task-edit-background : #dddddd50;

    /**
     * Editor border
     */
    --b-task-board-simple-task-edit-border     : null;

    /**
     * Editor box shadow
     */
    --b-task-board-simple-task-edit-box-shadow : null;
}

.b-simple-task-editor.b-editor.b-widget {
    background-color                           : transparent;
    box-shadow                                 : none;
    border-radius                              : 0;
    height                                     : auto !important;
    overflow                                   : visible;
    /* Above cards */
    z-index                                    : 3;

    --b-text-field-outlined-border-width       : 0;
    --b-text-field-outlined-focus-border-width : 0;
    --b-text-field-outlined-background         : transparent;
    --b-text-field-outlined-focus-background   : transparent;

    input {
        padding : 0;
        color   : inherit;
    }

    &::before {
        content       : '';

        position      : absolute;

        top           : -.5em;
        right         : -.5em;
        bottom        : -.5em;
        left          : -.5em;

        border-radius : 3px;

        border        : var(--b-task-board-simple-task-edit-border);
        box-shadow    : var(--b-task-board-simple-task-edit-box-shadow);
        background    : var(--b-task-board-simple-task-edit-background);
    }

    /* Reduce resource combo padding when used as inline editor */
    .b-resources-combo.b-combo {
        /* Make sure close icons are shown (since we shrink padding) */
        overflow : visible;

        input {
            /* Cannot hide with display: none, it affects functionality  */
            width : 0;
            flex  : unset;
        }

        .b-chip-view {
            overflow : visible;
            padding  : 0;
        }
    }

    /* Move expander icon closer to the edge in combos */
    [data-ref=expand] {
        margin-inline-end : 0;
    }
}

.b-task-board-task-item.b-editing {
    /* Hide item being edited. Has to occupy space and be considered visible for editor to align correctly */
    opacity : 0;
}
