.b-task-board-column-sort {
    order : 300;

    &:not(.b-asc, .b-desc) {
        opacity : 0.3;

        .b-icon-sort::before {
            content : "\f0ec";
            rotate  : 90deg;
        }
    }

    &.b-asc, &.b-desc {
        opacity : 1;
    }

    &.b-asc .b-icon-sort::before {
        content : "\f15d";
    }

    &.b-desc .b-icon-sort::before {
        content : "\f881";
    }
}

.b-column-sort-menu {
    .b-menu-item-icon {
        order      : 1;
        visibility : hidden;
    }

    span.b-menu-text {
        margin-inline : 0 .5em;
    }

    .b-checked {
        .b-menu-item-icon {
            visibility : visible;
        }

        &.b-asc .b-menu-item-icon::before {
            content : "\f15d";
        }

        &.b-desc .b-menu-item-icon::before {
            content : "\f881";
        }
    }
}

.b-task-board-column-title-sortable {
    cursor : pointer;
}
