.b-resources-picker {
    .b-selected-icon {
        display : none;
    }

    .b-resource-avatar {
        font-size  : .8em;
        opacity    : 0.5;
        transition : opacity .2s
    }

    .b-selected {
        font-weight : 600;

        .b-resource-avatar {
            opacity : 1;
        }
    }
}

.b-resources-combo.b-combo.b-uses-chip-view {

    --b-combo-chip-view-margin-block  : 0;
    --b-avatar-size                   : 2em;
    --b-chip-view-chip-border-width   : 0;
    --b-chip-view-chip-padding-inline : 0;
    --b-chip-view-chip-padding-block  : 0;
    /* Unsymmetrical padding to get same height as other inputs, avatars are a bit larger than normal content */
    --b-text-field-input-padding      : 0.75em 1em 0.75em 1em;
    --b-combo-chip-view-padding-top   : 0.75em;

    .b-chip-content {
        overflow : visible;
    }

    .b-chip {
        position : relative;
        overflow : visible;

        &:hover,
        &.b-selected {
            background-color : transparent;
            /* color            : $ field-color; */

            .b-close-icon {
                opacity : .7;
            }
        }
    }

    .b-close-icon {
        position      : absolute;
        background    : var(--b-neutral-90);
        color         : var(--b-neutral-40);
        opacity       : 0;
        border-radius : 50%;
        aspect-ratio  : 1 / 1;
        width         : 1.25em;
        display       : grid;
        place-items   : center;
        transition    : opacity .2s;
        top           : -.4em;
        right         : -.4em;
        font-size     : .8em;
    }

    input {
        height : 2em; /* Match avatar size, to give field stable height when empty */
    }
}

.b-resources-combo-avatar-container {
    margin-inline-start : var(--b-widget-padding);

    & ~ input {
        padding-inline-start : 0.5em;
    }

    .b-resource-avatar {
        width  : 1.8em;
        height : 1.8em;
    }
}
