.b-column-lock {
    .b-task-board-column-header {
        &.b-task-board-column-start, &.b-task-board-column-end {
            position : sticky;
            z-index  : 1;
        }

        &.b-task-board-column-start {
            inset-inline-start : 0;
        }

        &.b-task-board-column-end {
            inset-inline-end : 0;

            .b-task-board-column-expander::before {
                scale: -1 1;
            }
        }
    }

    /* Force overflow visible, side effect is that you cannot scroll columns individually. But there is no easy way of */
    /* allowing both */
    .b-task-board-swimlane[data-lane=default] {
        overflow : visible;

        .b-task-board-swimlane-body {
            overflow : visible;
        }
    }
}

.b-task-board-column:not(.b-task-board-drag-proxy) {
    &.b-task-board-column-start, &.b-task-board-column-end {
        position : sticky;
        height   : 100%;
        z-index  : 1;

        &.b-task-board-drag-proxy {
            position         : absolute;
            background-color : transparent;
        }
    }

    &.b-task-board-column-start {
        inset-inline-start : 0;
    }

    &.b-task-board-column-end {
        inset-inline-end : 0;
    }
}
