.b-task-board-base.b-filter-bar {
    --b-task-board-column-header-sticky-height : 8em;
}

.b-task-board-column-header-multiline {
    flex-direction : column;

    &.b-collapsed:not(.b-collapsing) {
        align-content : flex-start;

        .b-task-board-column-header-padder {
            align-items : center;
        }

        .b-task-board-column-filter-bar {
            visibility : hidden;
        }
    }

    .b-task-board-column-header-padder {
        min-width : 100%;

        &:not(:first-child) {
            padding-top : 0;
        }

        &:not(:last-child) {
            padding-bottom : 0;
        }
    }
}

.b-task-board-column-filter-bar {
    margin-top : .5em;
}

